import React from 'react';
import {hot} from 'react-hot-loader'
import './list.css'
import {
    TransitionGroup,CSSTransition
} from 'react-transition-group'
import {
    BrowserRouter as
    Router,
    Route,
    withRouter,
    Link,
    Switch,
    Prompt,
}from 'react-router-dom'

import {routers} from '@/router/router'

const Content = withRouter(({history,location,match}) => {
    console.log(JSON.stringify(match));
    return(
        <div className='list'>
            <p onClick={() => history.push(`${match.path}/view0`)}>0React hash </p>
            <p onClick={() => history.push(`${match.path}/view1`)}>1React Route   </p>
            <p onClick={() => history.push(`${match.path}/view2`)}>2React </p>
            <p onClick={() => history.push(`${match.path}/view3`)}>3</p>
            <p onClick={() => history.push(`${match.path}/view4`)}>4404 </p>
            <p onClick={() => history.push(`${match.path}/view5`)}>5Router  /  /</p>
            <p onClick={() => history.push(`${match.path}/view6`)}>6Route Render componentrenderchildren</p>
            <p onClick={() => history.push(`${match.path}/view7`)}>7React Router Api</p>
            <p onClick={() => history.push(`${match.path}/404`)}>8404 </p>
        </div>
    )
})


class List extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return(
            <div className='app-container'>
                <Content />
                <TransitionGroup>
                    <CSSTransition key={this.props.location.key} classNames='fade' timeout={300}>
                        <Switch location={this.props.location}>
                            {
                                routers.map((route,index) => {
                                    return(
                                        <Route
                                        key={index}
                                        exact
                                        path={`${this.props.match.path}` + route.path}
                                        component={route.component}
                                        />
                                    )
                                })
                            }
                        </Switch>
                    </CSSTransition>
                </TransitionGroup>
            </div>
        )
    }
}


export default hot(module)(List);


